%20%20%20%20%20%20%20%20%3Cuse%20fill%3D%22black%22%20fill-opacity%3D%221%22%20filter%3D%22url(%23filter-2)%22%20xlink%3Ahref%3D%22%23path-1%22%3E%3C%2Fuse%3E%0A%20%20%20%20%20%20%20%20%3Cuse%20fill%3D%22%234990E2%22%20fill-rule%3D%22evenodd%22%20xlink%3Ahref%3D%22%23path-1%22%3E%3C%2Fuse%3E%0A%20%20%20%20%20%20%20%20%3Cuse%20fill%3D%22black%22%20fill-opacity%3D%221%22%20filter%3D%22url(%23filter-3)%22%20xlink%3Ahref%3D%22%23path-1%22%3E%3C%2Fuse%3E%0A%20%20%20%20%20%20%20%20%3Cuse%20fill%3D%22black%22%20fill-opacity%3D%221%22%20filter%3D%22url(%23filter-4)%22%20xlink%3Ahref%3D%22%23path-1%22%3E%3C%2Fuse%3E%0A%20%20%20%20%3C%2Fg%3E%0A%3C%2Fsvg%3E');
		display: block;
		width: 38px;
		margin: 156px auto 28px auto;
	}

	.title{
		color: #333333;
		font-size: 22px;
		font-weight: 600;
		text-align: center;
		text-shadow: 1px 1px 0px rgba(255,255,255,0.87);
		width: 460px;
		margin: 0 auto 14px auto;
	}

	.content{
		color: #4A4A4A;
		font-size: 15px;
		text-shadow: 1px 1px 0px rgba(255,255,255,0.87);
		text-align: center;
		width: 460px;
		margin: 0 auto;
	}

	form{
		text-align: center;
		width: 460px;
		margin: 0 auto;
		padding: 0;
		input{
			color: #000;
			font-size: 15px;
			line-height: 36px;
			background: none;
			border: none;
			border-bottom: 1px solid rgba(0, 0, 0, 0.24);
			box-shadow: 0 1px 0 rgba(255, 255, 255, 0.4);
			outline: none;
			margin: 0;
			padding: 0 8px;
			width: 304px;
			height: 36px;
		}
	}

	.operations{
		text-align: center;
		width: 460px;
		margin: 24px auto;
		padding-top: 8px;
		border-top: 1px solid rgba(0, 0, 0, 0.24);
		box-shadow: 0 -1px 0 rgba(255, 255, 255, 0.4);
		letter-spacing: -4px;
		white-space: -4px;
		font-size: 0;
		button{
			font-size: 16px;
			line-height: 36px;
			text-align: center;
			text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.87);
			border: none;
			background: none;
			outline: none;
			display: inline-block;
			width: 120px;
			height: 36px;
			margin: 0;
			padding: 0;
			cursor: pointer;

			&.cancel{
				color: #6B6B6B;
				font-weight: lighter;
				&:hover{
					color: lighten(#6B6B6B, 16%);
				}
			}

			&.ok{
				color: #4A90E2;
				font-weight: bolder;
				&:hover{
					color: lighten(#4A90E2, 16%);
				}
			}
		}
	}
}

@keyframes enter{
	0%{
		border-radius: 100%;
		opacity: 0;
		transform: scale(0, 0);
	}
	100%{
		border-radius: 0;
		opacity: 1;
		transform: scale(1, 1);
	}
}